import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'
import * as firebase from 'firebase'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    loadedPeserta: [],
    user: null,
    nilai: [],
    loading: false
  },
  mutations: {
    register (state, payload) {
      state.loadedPeserta.push(payload)
    },
    setUser (state, payload) {
      state.user = payload
    },
    setLoadedPeserta (state, payload) {
      state.loadedPeserta = payload
    },
    setLoadedNilai (state, payload) {
      state.nilai = payload
    },
    inputNilai (state, payload) {
      state.nilai.push(payload)
    },
    loading (state, payload) {
      state.loading = payload
    }
  },
  actions: {
    loadNilai ({commit}) {
      firebase.database().ref('nilai Juri').once('value')
        .then((data) => {
          const nilai = []
          const obj = data.val()
          for (let key in obj) {
            nilai.push({
              id: key,
              idPeserta: obj[key].idPeserta,
              nama: obj[key].nama,
              namaJuri: obj[key].namaJuri,
              nilai1: obj[key].nilai1,
              nilai2: obj[key].nilai2,
              nilai3: obj[key].nilai3
            })
          }
          commit('setLoadedNilai', nilai)
        })
        .catch(
          (error) => {
            console.log(error)
          }
        )
    },
    inputkanNilai ({commit, getters}, payload) {
      const nilai = {
        idPeserta: payload.idPeserta,
        namaJuri: getters.user.name,
        nama: payload.nama,
        nilai1: payload.nilai1,
        nilai2: payload.nilai2,
        nilai3: payload.nilai3
      }
      firebase.database().ref('nilai Juri').push(nilai)
      commit('inputNilai', nilai)
    },
    loadPeserta ({commit}) {
      firebase.database().ref('peserta').once('value')
        .then((data) => {
          const peserta = []
          const obj = data.val()
          for (let key in obj) {
            peserta.push({
              id: key,
              nama_lengkap: obj[key].nama_lengkap,
              nama_panggilan: obj[key].nama_panggilan,
              tempat_lahir: obj[key].tempat_lahir,
              tanggal_lahir: obj[key].tanggal_lahir,
              alamat: obj[key].alamat,
              telp: obj[key].telp,
              pendidikan_saat_ini: obj[key].pendidikan_saat_ini,
              nama_sma_universitas: obj[key].nama_sma_universitas,
              tinggi_badan: obj[key].tinggi_badan,
              berat_badan: obj[key].berat_badan,
              hobi: obj[key].hobi,
              bahasa: obj[key].bahasa,
              nama_ortu: obj[key].nama_ortu,
              pekerjaan_ortu: obj[key].pekerjaan_ortu,
              alamat_kantor: obj[key].alamat_kantor,
              wakil_dari: obj[key].wakil_dari,
              imageKtpUrl: obj[key].imageKtpUrl,
              imageCloseupUrl: obj[key].imageCloseupUrl,
              imageFullUrl: obj[key].imageFullUrl
            })
          }
          commit('setLoadedPeserta', peserta)
        })
        .catch(
          (error) => {
            console.log(error)
          }
        )
    },
    register ({commit}, payload) {
      commit('loading', true)
      const orang = {
        nama_lengkap: payload.nama_lengkap,
        nama_panggilan: payload.nama_panggilan,
        tempat_lahir: payload.tempat_lahir,
        tanggal_lahir: payload.tanggal_lahir,
        alamat: payload.alamat,
        telp: payload.telp,
        pendidikan_saat_ini: payload.pendidikan_saat_ini,
        nama_sma_universitas: payload.nama_sma_universitas,
        tinggi_badan: payload.tinggi_badan,
        berat_badan: payload.berat_badan,
        hobi: payload.hobi,
        bahasa: payload.bahasa,
        nama_ortu: payload.nama_ortu,
        pekerjaan_ortu: payload.pekerjaan_ortu,
        alamat_kantor: payload.alamat_kantor,
        wakil_dari: payload.wakil_dari
      }
      let imageKtpUrl
      let imageCloseupUrl
      let imageFullUrl
      let key
      firebase.database().ref('peserta').push(orang)
        .then((data) => {
          key = data.key
          return key
        })
        .then(key => {
          const filenameKtp = payload.imageKtp.name
          const extKtp = filenameKtp.slice(filenameKtp.lastIndexOf('.'))
          return firebase.storage().ref('peserta/' + key + '/' + key + 'ktp' + extKtp).put(payload.imageKtp)
        })
        .then(fileDataKtp => {
          imageKtpUrl = fileDataKtp.metadata.downloadURLs[0]
          return firebase.database().ref('peserta').child(key).update({imageKtpUrl: imageKtpUrl})
        })
        .then(param => {
          const filenameCloseup = payload.imageCloseup.name
          const extCloseup = filenameCloseup.slice(filenameCloseup.lastIndexOf('.'))
          return firebase.storage().ref('peserta/' + key + '/' + key + 'closeup' + extCloseup).put(payload.imageCloseup)
        })
        .then(fileDataCloseup => {
          imageCloseupUrl = fileDataCloseup.metadata.downloadURLs[0]
          return firebase.database().ref('peserta').child(key).update({imageCloseupUrl: imageCloseupUrl})
        })
        .then(pram => {
          const filenameFull = payload.imageFull.name
          const extFull = filenameFull.slice(filenameFull.lastIndexOf('.'))
          return firebase.storage().ref('peserta/' + key + '/' + key + 'full' + extFull).put(payload.imageFull)
        })
        .then(fileDataFull => {
          imageFullUrl = fileDataFull.metadata.downloadURLs[0]
          return firebase.database().ref('peserta').child(key).update({imageFullUrl: imageFullUrl})
        })
        .then((data) => {
          commit('register', {
            ...orang,
            imageKtpUrl: imageKtpUrl,
            imageCloseupUrl: imageCloseupUrl,
            imageFullUrl: imageFullUrl,
            id: key
          })
          router.push('/peserta/' + key)
          commit('loading', false)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    signUserUp ({commit}, payload) {
      firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
        .then(
          user => {
            const newUser = {
              id: user.uid,
              name: payload.name
            }
            firebase.database().ref('juri').push(newUser)
              .then(
                commit('setUser', newUser)
              )
              .catch((error) => {
                console.log(error)
              })
          }
        )
        .catch((error) => {
          console.log(error)
        })
    },
    signUserIn ({commit}, payload) {
      firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
        .then(
          // user => {
          //   const newUser = {
          //     id: user.uid
          //   }
          // }
        )
        .catch((error) => {
          console.log(error)
        })
    },
    autoSignIn ({commit, getters}, payload) {
      const uid = payload.uid
      firebase.database().ref('juri').orderByChild('id').equalTo(uid).on('value', (snapshot) => {
        const obj = snapshot.val()
        // start get the key
        var arr = snapshot.val()
        var arr2 = Object.keys(arr)
        var key = arr2[0]
        // done obj[key].property to access
        let user = {
          id: obj[key].id,
          name: obj[key].name
        }
        commit('setUser', user)
      }, function (error) {
        console.log('Error: ' + error.code)
      })
    },
    logout ({commit}) {
      firebase.auth().signOut()
      commit('setUser', null)
    }
  },
  getters: {
    loadedPeserta (state) {
      return state.loadedPeserta
    },
    loadedOrang (state) {
      return (orangId) => {
        return state.loadedPeserta.find((orang) => {
          return orang.id === orangId
        })
      }
    },
    user (state) {
      return state.user
    },
    nilai (state) {
      return state.nilai
    },
    loading (state) {
      return state.loading
    }
  }

})



