<template>
  <v-container>
    <v-layout row wrap v-for="orang in peserta" :key="orang.id" mb-2>
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
        <v-card style="cursor:pointer;" :to="'/peserta/' + orang.id">
          <v-card-title>
            <h2>{{orang.nama_lengkap}}</h2>
          </v-card-title>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  computed: {
    peserta () {
      return this.$store.getters.loadedPeserta
    }
  }
}
</script>




