<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs6 offset-xs3>
        <v-dialog v-model="dialog" max-width="500px">
          <v-card>
            <v-card-title>
              <span class="headline">Nilai Peserta</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm6 md4>
                    <v-text-field label="Penampilan" v-model="nilai1" required></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field label="Wawasan" v-model="nilai2" required></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field label="Kejelasan" v-model="nilai3" required></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click.native="dialog = false">Close</v-btn>
              <v-btn color="blue darken-1" flat @click="inputNilai">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
         <v-btn v-if="userIsAuthenticated" color="primary" dark @click="dialog = true">Nilai</v-btn>
         <div v-if="!userIsAuthenticated">
           <v-alert color="success" icon="check_circle" value="true">
            <h3>Selamat anda telah terdaftar sebagai peserta Jegeg Bagus Gianyar</h3>
            <small>Jika Mengalami kendala dalam pendaftaran, hubungi 081234567890</small>
           </v-alert>
         </div>
        <v-card style="padding: 20px;">
          <h1>{{orang.nama_lengkap}}</h1>
          <h3>Nama Panggilan</h3>
          <p>{{orang.nama_panggilan}}</p>
          <h3>Tempat Lahir</h3>
          <p>{{orang.tempat_lahir}}</p>
          <h3>Tanggal Lahir</h3>
          <p>{{orang.tanggal_lahir}}</p>
          <h3>Almat</h3>
          <p>{{orang.alamat}}</p>
          <h3>Telp / Hp</h3>
          <p>{{orang.telp}}</p>
          <h3>Pendidikan Saat Ini</h3>
          <p>{{orang.pendidikan_saat_ini}}</p>
          <h3>Nama SMA / Universitas</h3>
          <p>{{orang.nama_sma_universitas}}</p>
          <h3>Tinggi Badan</h3>
          <p>{{orang.tinggi_badan}}</p>
          <h3>Berat Badan</h3>
          <p>{{orang.berat_badan}}</p>
          <h3>Hobi</h3>
          <p>{{orang.hobi}}</p>
          <h3>Bahasa</h3>
          <p>{{orang.bahasa}}</p>
          <h3>Nama Orangtua</h3>
          <p>{{orang.nama_ortu}}</p>
          <h3>Pekerjaan Orangtua</h3>
          <p>{{orang.pekerjaan_ortu}}</p>
          <h3>Alamat Kantor</h3>
          <p>{{orang.alamat_kantor}}</p>
          <h3>Wakil Dari</h3>
          <p>{{orang.wakil_dari}}</p>
          <h3>KTP</h3>
          <img :src="orang.imageKtpUrl" width="100%">
          <h3>Foto Close Up</h3>
          <img :src="orang.imageCloseupUrl" width="100%">
          <h3>Foto Full Body</h3>
          <img :src="orang.imageFullUrl" width="100%">
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: ['id'],
  data () {
    return {
      dialog: false,
      nilai1: '',
      nilai2: '',
      nilai3: ''
    }
  },
  methods: {
    inputNilai () {
      this.dialog = false
      const nilai = {
        idPeserta: this.id,
        nama: this.orang.nama_lengkap,
        nilai1: this.nilai1,
        nilai2: this.nilai2,
        nilai3: this.nilai3
      }
      this.$store.dispatch('inputkanNilai', nilai)
    }
  },
  computed: {
    orang () {
      return this.$store.getters.loadedOrang(this.id)
    },
    user () {
      return this.$store.getters.user()
    },
    userIsAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    }
  }
}
</script>




