<template>
  <v-container>
    <v-layout>
      <v-flex xs12>
        <form @submit.prevent="onRegister">
          <v-card>
            <div class="text-xs-center">
              <img height="150" src="../assets/logo.png">
            </div>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                  <h2 class="text-sm-center mb-5">Pendaftaran Peserta Jegeg Bagus Gianyar</h2>
                  <v-text-field name="nama_lengkap" label="Nama Lengkap" id="nama_lengkap" v-model="nama_lengkap"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                  <v-text-field name="nama_panggilan" label="Nama Panggilan" id="nama_panggilan" v-model="nama_panggilan"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                  <v-text-field name="tempat_lahir" label="Tempat Lahir" id="tempat_lahir" v-model="tempat_lahir"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                  <v-menu lazy :close-on-content-click="false" v-model="menu" transition="scale-transition" offset-y full-width :nudge-right="40"
                    max-width="290px" min-width="290px">
                    <v-text-field slot="activator" label="Tanggal Lahir" v-model="tanggal_lahir" prepend-icon="event" readonly></v-text-field>
                    <v-date-picker type="date" v-model="tanggal_lahir" no-title scrollable actions autosave>
                    </v-date-picker>
                  </v-menu>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                  <v-text-field name="alamat" label="Alamat" id="alamat" v-model="alamat" multi-line></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                  <v-text-field name="telp" label="No. Telp / HP" id="telp" v-model="telp"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field name="pendidikan_saat_ini" label="Pendidikan Saat Ini" id="telp" v-model="pendidikan_saat_ini"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field name="nama_sma_universitas" label="Nama SMA / Universitas" id="nama_sma_universitas" v-model="nama_sma_universitas"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field
                name="tinggi_badan"
                label="Tinggi Badan"
                id="tinggi_badan"
                v-model="tinggi_badan"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field
                name="berat_badan"
                label="Berat Badan"
                id="berat_badan"
                v-model="berat_badan"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field
                name="hobi"
                label="Hobi"
                id="hobi"
                v-model="hobi"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field
                name="bahasa"
                label="Bahasa"
                id="bahasa"
                v-model="bahasa"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field
                name="nama_ortu"
                label="Nama Orang Tua"
                id="nama_ortu"
                v-model="nama_ortu"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field
                name="pekerjaan_ortu"
                label="Pekerjaan Orang Tua"
                id="pekerjaan_ortu"
                v-model="pekerjaan_ortu"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field
                name="alamat_kantor"
                label="Alamat Kantor"
                id="alamat_kantor"
                v-model="alamat_kantor"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-text>
                <v-text-field
                name="wakil_dari"
                label="Wakil Dari"
                id="wakil_dari"
                v-model="wakil_dari"></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-actions>
                <v-btn raised class="primary" @click="ktpPickFile"><v-icon dark>add</v-icon> Upload KTP</v-btn>
                  <input
                    type="file"
                    style="display: none"
                    ref="ktpInput"
                    accept="image/*"
                    @change="ktpFilePicked">
                </v-card-actions> 
              </v-flex>
            </v-layout>
            <v-layout row>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-media>
                <img :src="imageKtpUrl">
                </v-card-media>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-actions>
                <v-btn raised class="primary" @click="closeupPickFile"><v-icon dark>add</v-icon> Upload Foto Close Up</v-btn>
                  <input
                    type="file"
                    style="display: none"
                    ref="closeupInput"
                    accept="image/*"
                    @change="closeupFilePicked">
                </v-card-actions>
              </v-flex>
            </v-layout>
            <v-layout row>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-media>
                <img :src="imageCloseupUrl">
                </v-card-media>
              </v-flex>
            </v-layout>

            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-actions>
                <v-btn raised class="primary" @click="fullPickFile"><v-icon dark>add</v-icon> Upload Foto Full Body</v-btn>
                  <input
                    type="file"
                    style="display: none"
                    ref="fullInput"
                    accept="image/*"
                    @change="fullFilePicked">
                </v-card-actions>
              </v-flex>
            </v-layout>
            <v-layout row>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-media>
                <img :src="imageFullUrl">
                </v-card-media>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 sm6 offset-sm3>
                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn block color="success" type="submit">Daftar</v-btn>
                <v-progress-linear v-if="loading" indeterminate></v-progress-linear>
                </v-card-actions>
              </v-flex>
            </v-layout>
          </v-card>
        </form>
        </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        menu: '',
        nama_lengkap: '',
        nama_panggilan: '',
        tempat_lahir: '',
        tanggal_lahir: '',
        alamat: '',
        telp: '',
        pendidikan_saat_ini: '',
        nama_sma_universitas: '',
        tinggi_badan: '',
        berat_badan: '',
        hobi: '',
        bahasa: '',
        nama_ortu: '',
        pekerjaan_ortu: '',
        alamat_kantor: '',
        wakil_dari: '',
        imageKtpUrl: '',
        imageCloseupUrl: '',
        imageFullUrl: '',
        imageCloseup: null,
        imageKtp: null,
        imageFull: null
      }
    },
    computed: {
      loading () {
        return this.$store.getters.loading === true
      }
    },
    methods: {
      onRegister () {
        const orangData = {
          nama_lengkap: this.nama_lengkap,
          nama_panggilan: this.nama_panggilan,
          tempat_lahir: this.tempat_lahir,
          tanggal_lahir: this.tanggal_lahir,
          alamat: this.alamat,
          telp: this.telp,
          pendidikan_saat_ini: this.pendidikan_saat_ini,
          nama_sma_universitas: this.nama_sma_universitas,
          tinggi_badan: this.tinggi_badan,
          berat_badan: this.berat_badan,
          hobi: this.hobi,
          bahasa: this.bahasa,
          nama_ortu: this.nama_ortu,
          pekerjaan_ortu: this.pekerjaan_ortu,
          alamat_kantor: this.alamat_kantor,
          wakil_dari: this.wakil_dari,
          imageKtp: this.imageKtp,
          imageCloseup: this.imageCloseup,
          imageFull: this.imageFull
        }
        this.$store.dispatch('register', orangData)
      },
      ktpPickFile () {
        this.$refs.ktpInput.click()
      },
      ktpFilePicked (event) {
        const files = event.target.files
        let filename = files[0].name
        if (filename.lastIndexOf('.') <= 0) {
          return alert('Please add a valid file!')
        }
        const fileReader = new FileReader()
        fileReader.addEventListener('load', () => {
          this.imageKtpUrl = fileReader.result
        })
        fileReader.readAsDataURL(files[0])
        this.imageKtp = files[0]
      },
      closeupPickFile () {
        this.$refs.closeupInput.click()
      },
      closeupFilePicked (event) {
        const files = event.target.files
        let filename = files[0].name
        if (filename.lastIndexOf('.') <= 0) {
          return alert('Please add a valid file!')
        }
        const fileReader = new FileReader()
        fileReader.addEventListener('load', () => {
          this.imageCloseupUrl = fileReader.result
        })
        fileReader.readAsDataURL(files[0])
        this.imageCloseup = files[0]
      },
      fullPickFile () {
        this.$refs.fullInput.click()
      },
      fullFilePicked (event) {
        const files = event.target.files
        let filename = files[0].name
        if (filename.lastIndexOf('.') <= 0) {
          return alert('Please add a valid file!')
        }
        const fileReader = new FileReader()
        fileReader.addEventListener('load', () => {
          this.imageFullUrl = fileReader.result
        })
        fileReader.readAsDataURL(files[0])
        this.imageFull = files[0]
      }
    }
  }
</script>



