<template>
  <v-container>
    <v-layout row wrap v-for="nilai in nilai" :key="nilai.id" mb-2>
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
        <v-card>
          <v-card-title>
            <h2>{{nilai.nama}}</h2>
          </v-card-title>
          <v-card-text>
            <p>Juri : {{nilai.namaJuri}}</p>
            <p>Penampilan : {{nilai.nilai1}}</p>
            <p>Wawasan : {{nilai.nilai2}}</p>
            <p>Kejelasan : {{nilai.nilai3}}</p>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  computed: {
    nilai () {
      return this.$store.getters.nilai
    }
  }
}
</script>


