import Vue from 'vue'
import Router from 'vue-router'
import Pendaftaran from '@/components/Pendaftaran'
import Peserta from '@/components/Peserta'
import Orang from '@/components/Orang'
import Signup from '@/components/Signup'
import Signin from '@/components/Signin'
import Nilai from '@/components/Nilai'
import AuthGuard from './auth-guard'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Pendaftaran',
      component: Pendaftaran
    },
    {
      path: '/peserta',
      name: 'Peserta',
      component: Peserta,
      beforeEnter: AuthGuard
    },
    {
      path: '/peserta/:id',
      name: 'Orang',
      props: true,
      component: Orang
    },
    {
      path: '/signup/123/321',
      name: 'Signup',
      component: Signup
    },
    {
      path: '/signin',
      name: 'Signin',
      component: Signin
    },
    {
      path: '/nilai',
      name: 'Nilai',
      component: Nilai
    }
  ]
})



