// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import { store } from './store'
import * as firebase from 'firebase'

Vue.use(Vuetify)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  created () {
    firebase.initializeApp({
      apiKey: 'AIzaSyC76RBaMr5sC27_SQ9pf17ZDdZHgkOvALs',
      authDomain: 'jb-prototype-7d530.firebaseapp.com',
      databaseURL: 'https://jb-prototype-7d530.firebaseio.com',
      projectId: 'jb-prototype-7d530',
      storageBucket: 'gs://jb-prototype-7d530.appspot.com',
      messagingSenderId: '647212663532'
    })
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn', user)
      } else {
        return null
      }
    })
    this.$store.dispatch('loadPeserta')
    this.$store.dispatch('loadNilai')
  }
})



