<template>
  <v-app>
    <v-toolbar dark>
      <v-toolbar-side-icon @click.stop="drawer = !drawer" class="hidden-md-and-up"></v-toolbar-side-icon>
      <v-toolbar-title><router-link to="/" tag="span" style="cursor: pointer">Jegegss Bagus Gianyar</router-link></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat v-if="userIsAuthenticated" to="/peserta"><v-icon>chrome_reader_mode</v-icon>Lihat Peserta</v-btn>
        <v-btn flat v-if="userIsAuthenticated" to="/nilai"><v-icon>book</v-icon>Lihat Nilai</v-btn>
        <v-btn flat v-if="userIsAuthenticated" @click="onLogout">Logout</v-btn>
        <v-btn flat v-if="!userIsAuthenticated" to="/signin"><v-icon>account_circle</v-icon> Juri</v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <router-view/>
    <v-navigation-drawer
      width="150"
      temporary
      v-model="drawer"
      light
      absolute
    >
      <v-list class="pt-0" dense>
        <v-list-tile to="/peserta" v-if="userIsAuthenticated">
          <v-list-tile-action >
            <v-icon>chrome_reader_mode</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Peserta</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-tile to="/nilai" v-if="userIsAuthenticated">
          <v-list-tile-action >
            <v-icon>book</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Nilai</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-tile @click="onLogout" v-if="userIsAuthenticated">
          <v-list-tile-action >
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Logout</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-tile to="/signin" v-if="!userIsAuthenticated">
          <v-list-tile-action>
            <v-icon>account_circle</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Juri</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
  </v-app>
</template>

<script>
export default {
  data () {
    return {
      drawer: null
    }
  },
  methods: {
    onLogout () {
      this.$store.dispatch('logout')
      this.$router.push('/')
    }
  },
  computed: {
    userIsAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    }
  }
}
</script>




